.promo {
  padding: 20px;
  padding-bottom: 35px;
  min-height: 290px
}

.promo__wrapper {
  background-image: url("../img/promo-layer.png"), url("../img/promo-background.jpg");
  background-repeat: no-repeat;
  background-position: top;
  padding-top: 25px;
  padding-left: 20px;
  padding-right: 20px;
  min-height: 290px;
}

.promo__text {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  display: block;
  margin: 0px;
  margin-bottom: 15px;
  text-align: center;
}
